"use client";
import { useState, useEffect } from 'react';
import { useAppStore } from '@/lib/store';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { Card, CardContent } from '@/components/ui/card';
import { Search, FileEdit, CheckCircle2, Loader2, StickyNote, Activity, User, Phone, BookOpen, AlertCircle, GraduationCap, Users } from 'lucide-react';
import { toast } from 'sonner';
import { getAppointments, updateConsultationRecord } from '@/app/actions';

export default function CasesPage() {
  const { currentUser } = useAppStore();
  const [records, setRecords] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  // 搜索和弹窗状态
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedRecord, setSelectedRecord] = useState<any>(null);
  const [editNotes, setEditNotes] = useState('');
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [saving, setSaving] = useState(false);

  // 1. 加载数据
  useEffect(() => {
    async function fetchData() {
      if (currentUser?.id) {
        try {
          const data = await getAppointments(currentUser.id, 'counselor');
          const validRecords = data.filter((r: any) => r.status !== 'rejected' && r.status !== 'pending');
          setRecords(validRecords);
        } catch (error) {
          toast.error("加载数据失败");
        } finally {
          setLoading(false);
        }
      }
    }
    fetchData();
  }, [currentUser]);

  // 2. 搜索过滤
  const filteredRecords = records.filter(r => 
    r.student?.name?.includes(searchTerm) || 
    r.studentIdNum?.includes(searchTerm) ||
    r.problemType?.includes(searchTerm)
  );

  // 3. 打开档案详情
  const handleOpenDialog = (record: any) => {
    setSelectedRecord(record);
    setEditNotes(record.notes || ''); 
    setIsDialogOpen(true);
  };

  // 4. 保存记录
  const handleSaveNotes = async () => {
    if (!selectedRecord) return;
    setSaving(true);
    const res = await updateConsultationRecord(selectedRecord.id, editNotes);
    
    if (res.success) {
      toast.success("咨询档案已归档");
      setIsDialogOpen(false);
      setRecords(prev => prev.map(r => 
        r.id === selectedRecord.id ? { ...r, notes: editNotes, status: 'completed' } : r
      ));
    } else {
      toast.error("保存失败");
    }
    setSaving(false);
  };

  const getScoreColor = (score: number, type: 'sds' | 'sas' | 'ses') => {
    if (!score) return "text-slate-500";
    if (type === 'ses') return score < 25 ? "text-red-600 font-bold" : "text-green-600 font-bold"; 
    return score > 50 ? "text-red-600 font-bold" : "text-green-600 font-bold";
  };

  return (
    <div className="space-y-6 h-full flex flex-col">
      {/* 头部区域 */}
      <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
        <div>
          <h1 className="text-2xl font-bold text-[#2C3E50] flex items-center gap-2">
            <BookOpen className="w-6 h-6 text-[#5D9C59]" /> 咨询个案档案库
          </h1>
          <p className="text-slate-500 mt-1 text-sm">管理学生咨询记录，查看详细病史与测评结果。</p>
        </div>
        <div className="relative w-full md:w-72">
          <Search className="absolute left-3 top-2.5 w-4 h-4 text-slate-400" />
          <Input 
            placeholder="搜索姓名、学号或问题类型..." 
            className="pl-9 bg-white shadow-sm" 
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
      </div>

      {/* 列表区域 */}
      <div className="bg-white rounded-xl border border-slate-200 shadow-sm overflow-hidden flex-1">
        <Table>
          <TableHeader className="bg-slate-50/80">
            <TableRow>
              <TableHead className="w-[200px]">学生基本信息</TableHead>
              <TableHead>咨询问题类型</TableHead>
              <TableHead>预约时间</TableHead>
              <TableHead>测评状态</TableHead>
              <TableHead>档案状态</TableHead>
              <TableHead className="text-right">操作</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {loading ? (
              <TableRow>
                <TableCell colSpan={6} className="h-32 text-center">
                  <div className="flex justify-center items-center gap-2 text-slate-500">
                    <Loader2 className="animate-spin w-4 h-4" /> 数据加载中...
                  </div>
                </TableCell>
              </TableRow>
            ) : filteredRecords.length === 0 ? (
              <TableRow>
                <TableCell colSpan={6} className="h-48 text-center text-slate-400">
                  没有找到符合条件的咨询记录
                </TableCell>
              </TableRow>
            ) : (
              filteredRecords.map((record) => (
                <TableRow key={record.id} className="hover:bg-slate-50/50 transition-colors">
                  <TableCell>
                    <div className="flex items-center gap-3">
                      <div className="w-9 h-9 rounded-full bg-blue-50 text-blue-600 flex items-center justify-center font-bold text-sm shrink-0">
                        {record.student?.name?.[0]}
                      </div>
                      <div>
                        <div className="font-bold text-slate-700">{record.student?.name}</div>
                        <div className="text-xs text-slate-400 font-mono">
                           {record.studentIdNum || '无学号'}
                        </div>
                      </div>
                    </div>
                  </TableCell>
                  <TableCell>
                    <Badge variant="secondary" className="bg-slate-100 text-slate-600">
                      {record.problemType || '常规咨询'}
                    </Badge>
                  </TableCell>
                  <TableCell>
                    <div className="text-sm font-medium text-slate-700">{new Date(record.date).toLocaleDateString()}</div>
                    <div className="text-xs text-slate-500">{record.time}</div>
                  </TableCell>
                  <TableCell>
                    {record.testResult ? (
                      <div className="flex flex-col gap-1">
                         <span className="text-xs font-bold text-green-600 flex items-center gap-1">
                           <CheckCircle2 className="w-3 h-3"/> 已完成
                         </span>
                      </div>
                    ) : (
                      <span className="text-xs text-slate-400 italic">未测评</span>
                    )}
                  </TableCell>
                  <TableCell>
                    {record.status === 'completed' ? (
                      <Badge className="bg-green-100 text-green-700 hover:bg-green-100 border-green-200">已归档</Badge>
                    ) : (
                      <Badge variant="outline" className="text-orange-600 border-orange-200 bg-orange-50">待记录</Badge>
                    )}
                  </TableCell>
                  <TableCell className="text-right">
                    <Button 
                      size="sm" 
                      className={`${record.status === 'completed' ? 'bg-slate-100 text-slate-700 hover:bg-slate-200' : 'bg-[#5D9C59] hover:bg-[#4a8546] text-white'}`}
                      onClick={() => handleOpenDialog(record)}
                    >
                      <FileEdit className="w-3.5 h-3.5 mr-1.5" /> 
                      {record.status === 'completed' ? '查看' : '记录'}
                    </Button>
                  </TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </div>

      {/* --- 修复后的咨询档案详情弹窗 --- */}
      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        {/* 关键修改：w-full max-w-7xl 强制变宽 */}
        <DialogContent className="w-[95vw] max-w-7xl h-[90vh] p-0 gap-0 flex flex-col overflow-hidden bg-slate-50">
          
          {/* 1. 弹窗头部 (固定) */}
          <DialogHeader className="px-6 py-4 border-b bg-white shrink-0">
            <DialogTitle className="flex items-center justify-between">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-green-50 rounded-lg">
                  <Activity className="w-5 h-5 text-[#5D9C59]" />
                </div>
                <div>
                  <h2 className="text-xl font-bold text-slate-800">学生心理咨询档案</h2>
                  <p className="text-xs text-slate-400 font-normal mt-0.5">档案编号: {selectedRecord?.id?.slice(-8).toUpperCase()}</p>
                </div>
              </div>
              <Badge variant={selectedRecord?.status === 'completed' ? 'default' : 'outline'} className={selectedRecord?.status === 'completed' ? 'bg-green-600' : 'text-orange-600 border-orange-200 bg-orange-50'}>
                {selectedRecord?.status === 'completed' ? '状态：已归档' : '状态：进行中'}
              </Badge>
            </DialogTitle>
          </DialogHeader>

          {/* 2. 弹窗主体内容 */}
          {/* 关键修改：flex-col md:flex-row (手机上下排，电脑左右排) */}
          <div className="flex-1 flex flex-col md:flex-row overflow-hidden">
            
            {/* --- 左侧栏：档案信息 (电脑上固定宽320px, 手机上全宽) --- */}
            <div className="w-full md:w-[320px] shrink-0 border-r bg-slate-50/50 overflow-y-auto p-6 space-y-6">
              
              {/* 卡片：基本信息 */}
              <div className="bg-white p-5 rounded-xl border shadow-sm space-y-4">
                 <div className="flex justify-between items-start border-b pb-4">
                    <div>
                        <h3 className="text-2xl font-bold text-slate-800">{selectedRecord?.student?.name}</h3>
                        <div className="flex items-center gap-2 mt-1">
                           <Badge variant="secondary" className="font-mono text-xs text-slate-500 bg-slate-100">
                             {selectedRecord?.studentIdNum || '无学号'}
                           </Badge>
                        </div>
                    </div>
                    <div className="w-12 h-12 bg-blue-50 rounded-full flex items-center justify-center text-blue-500 font-bold text-xl">
                        {selectedRecord?.student?.name?.[0]}
                    </div>
                 </div>

                 <div className="space-y-3 text-sm">
                    <div className="grid grid-cols-[60px_1fr] gap-2 items-center">
                        <span className="text-slate-400 flex items-center gap-1"><GraduationCap className="w-3.5 h-3.5"/> 学院</span>
                        <span className="font-medium text-slate-700 truncate" title={selectedRecord?.college}>{selectedRecord?.college || '-'}</span>
                    </div>
                    <div className="grid grid-cols-[60px_1fr] gap-2 items-center">
                        <span className="text-slate-400 flex items-center gap-1"><BookOpen className="w-3.5 h-3.5"/> 专业</span>
                        <span className="font-medium text-slate-700 truncate" title={selectedRecord?.major}>{selectedRecord?.major || '-'}</span>
                    </div>
                    <div className="grid grid-cols-[60px_1fr] gap-2 items-center">
                        <span className="text-slate-400 flex items-center gap-1"><Users className="w-3.5 h-3.5"/> 班级</span>
                        <span className="font-medium text-slate-700 truncate">{selectedRecord?.classInfo || '-'}</span>
                    </div>
                    <div className="grid grid-cols-[60px_1fr] gap-2 items-center pt-2 border-t mt-2">
                        <span className="text-slate-400 flex items-center gap-1"><Phone className="w-3.5 h-3.5"/> 电话</span>
                        <span className="font-mono text-slate-700">{selectedRecord?.phone || '-'}</span>
                    </div>
                 </div>
              </div>

              {/* 卡片：背景与现状 */}
              <div className="space-y-4">
                 <h4 className="text-xs font-bold text-slate-400 uppercase tracking-wider pl-1">背景与现状</h4>
                 
                 <div className="grid grid-cols-2 gap-3">
                    <Card className="shadow-sm border-orange-100 bg-orange-50/50">
                      <CardContent className="p-3">
                         <span className="text-xs text-orange-400 block mb-1">主要问题</span>
                         <span className="font-bold text-orange-700 text-sm">{selectedRecord?.problemType || '未填写'}</span>
                      </CardContent>
                    </Card>
                    <Card className="shadow-sm">
                      <CardContent className="p-3">
                         <span className="text-xs text-slate-400 block mb-1">身心状态</span>
                         <span className="font-medium text-slate-700 text-sm">{selectedRecord?.physicalState || '良好'}</span>
                      </CardContent>
                    </Card>
                 </div>
                 
                 <div className="bg-white p-4 rounded-lg border shadow-sm text-sm">
                    <div className="flex items-center gap-2 mb-2 text-slate-500">
                        <AlertCircle className="w-4 h-4"/> <span className="font-bold text-xs uppercase">精神疾病史</span>
                    </div>
                    <p className="text-slate-700 leading-relaxed bg-slate-50 p-2 rounded">
                        {selectedRecord?.mentalHistory && selectedRecord.mentalHistory !== '无' 
                            ? selectedRecord.mentalHistory 
                            : '无相关记录'}
                    </p>
                 </div>
              </div>

              {/* 卡片：测评数据 */}
              {selectedRecord?.testResult && (
                <div className="space-y-2">
                   <h4 className="text-xs font-bold text-slate-400 uppercase tracking-wider pl-1">心理测评前测数据</h4>
                   <div className="bg-white border rounded-lg overflow-hidden shadow-sm">
                     <div className="grid grid-cols-3 divide-x text-center py-4">
                        <div>
                           <div className="text-[10px] uppercase text-slate-400 mb-1">SDS 抑郁</div>
                           <div className={`text-xl ${getScoreColor(selectedRecord.testResult.sdsScore, 'sds')}`}>
                             {selectedRecord.testResult.sdsScore}
                           </div>
                        </div>
                        <div>
                           <div className="text-[10px] uppercase text-slate-400 mb-1">SAS 焦虑</div>
                           <div className={`text-xl ${getScoreColor(selectedRecord.testResult.sasScore, 'sas')}`}>
                             {selectedRecord.testResult.sasScore}
                           </div>
                        </div>
                        <div>
                           <div className="text-[10px] uppercase text-slate-400 mb-1">SES 自尊</div>
                           <div className={`text-xl ${getScoreColor(selectedRecord.testResult.sesScore, 'ses')}`}>
                             {selectedRecord.testResult.sesScore}
                           </div>
                        </div>
                     </div>
                  </div>
                </div>
              )}
            </div>

            {/* --- 右侧栏：SOAP记录 (剩余宽度, 独立滚动) --- */}
            <div className="flex-1 flex flex-col bg-white h-full overflow-hidden min-h-[400px]">
               <div className="p-6 border-b bg-slate-50/30 flex justify-between items-center shrink-0">
                  <Label className="text-base font-bold text-slate-800 flex items-center gap-2">
                     <StickyNote className="w-4 h-4 text-[#5D9C59]"/> 
                     咨询过程记录 (SOAP)
                  </Label>
                  <div className="hidden md:block text-xs text-slate-500 bg-white px-2 py-1 rounded border">
                     预约时间: {new Date(selectedRecord?.date).toLocaleDateString()} {selectedRecord?.time}
                  </div>
               </div>
               
               <div className="flex-1 p-6 overflow-y-auto">
                  <Textarea 
                    className="w-full h-full min-h-[300px] p-4 text-sm leading-relaxed border-slate-200 focus:ring-[#5D9C59] focus:border-[#5D9C59] resize-none font-mono"
                    placeholder={`S (Subjective) 主诉:\n- 来访者主要表达了...\n\nO (Objective) 观察:\n- 精神状态、衣着、行为表现...\n- 测评结果分析...\n\nA (Assessment) 评估:\n- 问题性质评估...\n- 风险等级评估...\n\nP (Plan) 计划:\n- 本次干预策略...\n- 课后作业...\n- 下次预约安排...`}
                    value={editNotes}
                    onChange={(e) => setEditNotes(e.target.value)}
                  />
               </div>
               
               <div className="p-4 border-t bg-slate-50 flex flex-col md:flex-row justify-between items-center gap-3 shrink-0">
                  <div className="hidden md:flex items-center gap-2 text-xs text-yellow-600 bg-yellow-50 px-3 py-1.5 rounded-full">
                     <AlertCircle className="w-3.5 h-3.5"/>
                     <span>请确保所有记录客观真实，危机个案需立即上报。</span>
                  </div>
                  <div className="flex gap-3 w-full md:w-auto">
                    <Button variant="outline" className="flex-1 md:flex-none" onClick={() => setIsDialogOpen(false)}>取消</Button>
                    <Button 
                        className="bg-[#5D9C59] hover:bg-[#4a8546] flex-1 md:flex-none md:min-w-[140px]" 
                        onClick={handleSaveNotes}
                        disabled={saving}
                    >
                        {saving ? <Loader2 className="w-4 h-4 animate-spin mr-2"/> : <CheckCircle2 className="w-4 h-4 mr-2"/>}
                        {saving ? '归档中...' : '保存并归档'}
                    </Button>
                  </div>
               </div>
            </div>

          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
}